header {
  flex-wrap: nowrap;
  justify-content: space-between;
  margin: 20px 15%;
  display: flex;
}

nav {
  flex-direction: row;
  align-self: flex-end;
  margin-left: 50%;
  display: flex;
}

nav p {
  margin-right: 2%;
  display: flex;
}

.barmenu {
  width: 40%;
}

header p {
  font-size: larger;
}

.tarjetas img {
  width: 50%;
}

img.cartel {
  width: 25%;
  height: 40%;
}

html {
  height: 100%;
  width: 100%;
}

body {
  margin: 0;
}

main {
  margin: 0 10% 10%;
  padding-bottom: 10%;
}

h1, h2 {
  color: #086f37;
}

.titleindex {
  color: #000;
  text-align: center;
  padding-bottom: 5%;
}

.tarjetas {
  text-align: center;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  display: flex;
}

.tarjeta1, .tarjeta2, .tarjeta3 {
  width: 30%;
}

footer p {
  color: #fff;
  text-align: center;
}

footer {
  width: 100%;
  background-color: #000;
  position: fixed;
  bottom: 0;
}

footer a:hover {
  color: #fff;
  font-weight: bold;
  text-decoration-line: none;
}

footer a, footer a:visited {
  color: #fff;
  text-decoration-line: none;
}

.parte2 {
  float: left;
  width: 20%;
  padding-bottom: 75px;
}

.parte1 {
  float: right;
  width: 80%;
  padding-bottom: 75px;
}

.specialp {
  font-weight: bold;
}

a:hover, a:visited {
  color: #000;
  font-weight: bold;
  text-decoration-line: none;
}

a {
  color: #000;
  font-weight: normal;
  text-decoration-line: none;
}

dt {
  font-weight: bold;
}

#map {
  height: 500px;
}

@media (max-width: 600px) {
  .tarjetas {
    flex-direction: column;
    align-items: center;
  }

  .tarjeta1, .tarjeta2, .tarjeta3 {
    width: 100%;
    text-align: center;
    flex-direction: column;
    align-items: center;
    display: flex;
  }

  .barmenu {
    width: 80%;
  }
}

